"use client";

import { useState, useEffect, useMemo } from "react";
import Image from "next/image";
import Link from "next/link";
import { User, ArrowRight } from "lucide-react";
import axios from "axios";

export default function Featured() {
  const [featuredContent, setFeaturedContent] = useState(null);

  useEffect(() => {
    let isMounted = true;

    const loadFeaturedContent = async () => {
      try {
        const response = await axios.get("/api/settings/homepage");
        if (!isMounted) return;

        if (response.data && response.data.content && response.data.content.featured) {
          setFeaturedContent(response.data.content.featured);
          return;
        }
      } catch (error) {
        console.error("Error loading featured content from API:", error);
      }

      // Set empty state if API fails or no data
      if (isMounted) {
        setFeaturedContent({ smbStories: [], herStories: [], socialStories: [] });
      }
    };

    loadFeaturedContent();

    return () => {
      isMounted = false;
    };
  }, []);

  const getAuthorName = (author) => {
    if (!author) return "Editorial Team";
    if (typeof author === "object" && author?.name) {
      return author.name;
    }
    return author;
  };

  // Load posts from separate subsections
  const { smbStories, herStories, socialStories } = useMemo(() => {
    // Handle both old format (array) and new format (object with subsections)
    let smb = [];
    let her = [];
    let social = [];

    if (featuredContent) {
      if (Array.isArray(featuredContent)) {
        // Old format: single array - distribute automatically for backward compatibility
        const normalizedPosts = featuredContent.map((post) => ({
          ...post,
          slug: post.slug || (post._id ? `post-${post._id}` : `post-${post.id}`),
          featuredImage: post.featuredImage || "/demo.png",
          author: post.author || { name: "Editorial Team" },
          excerpt: post.excerpt || "",
        }));
        smb = normalizedPosts.slice(0, 3);
        her = normalizedPosts.slice(3, 8);
        social = normalizedPosts.slice(8);
      } else if (typeof featuredContent === 'object') {
        // New format: object with subsections
        const normalizePost = (post) => ({
          ...post,
          slug: post.slug || (post._id ? `post-${post._id}` : `post-${post.id}`),
          featuredImage: post.featuredImage || "/demo.png",
          author: post.author || { name: "Editorial Team" },
          excerpt: post.excerpt || "",
        });

        smb = (featuredContent.smbStories || []).map(normalizePost);
        her = (featuredContent.herStories || []).map(normalizePost);
        social = (featuredContent.socialStories || []).map(normalizePost);
      }
    }

    return {
      smbStories: smb,
      herStories: her,
      socialStories: social,
    };
  }, [featuredContent]);

  return (
    <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20">
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Featured Header - Modern 2026 Style */}
        <div className="mb-16">
          <div className="mb-8">
            <h2 className="text-5xl md:text-6xl lg:text-7xl font-black text-transparent bg-clip-text bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-200 dark:to-white tracking-tight leading-tight">
              Featured
            </h2>
            <div className="mt-3 h-1 w-32 bg-gradient-to-r from-red-500 via-red-600 to-transparent dark:from-red-400 dark:via-red-500 dark:to-transparent rounded-full"></div>
          </div>
        </div>

        {/* Three Columns - Modern 2026 Design */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 lg:gap-10">
          {/* Column 1: SMB Story */}
          <div className="space-y-6">
            <div className="bg-gradient-to-r from-gray-900 to-gray-800 dark:from-white dark:to-gray-100 px-5 py-4 rounded-xl shadow-lg border border-gray-800/50 dark:border-gray-200/50 backdrop-blur-sm">
              <h3 className="text-white dark:text-gray-900 font-black text-base uppercase tracking-widest">SMB Story</h3>
            </div>

            {smbStories.length > 0 ? (
              <>
                {/* Main Article - Modern Glassmorphism */}
                <Link href={`/post?slug=${smbStories[0].slug}`} className="group block">
                  <div className="bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 dark:from-slate-800 dark:via-slate-900 dark:to-slate-800 p-6 rounded-xl shadow-xl hover:shadow-2xl transition-all duration-500 relative overflow-hidden border border-gray-800/50 dark:border-slate-700/50 backdrop-blur-sm hover:scale-[1.02]">
                    {/* Decorative gradient overlay */}
                    <div className="absolute top-0 right-0 w-40 h-40 bg-red-600/10 rounded-full blur-3xl group-hover:bg-red-600/20 transition-all duration-500"></div>
                    
                    <h4 className="text-lg font-bold mb-3 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-400 group-hover:to-red-300 transition-all duration-500 line-clamp-2 relative z-10 text-white dark:text-white">
                      {smbStories[0].title}
                    </h4>
                    {smbStories[0].excerpt && (
                      <p className="text-sm text-gray-300 dark:text-gray-300 mb-4 line-clamp-3 leading-relaxed relative z-10">
                        {smbStories[0].excerpt}
                      </p>
                    )}
                    <div className="flex items-center justify-between relative z-10">
                      <div className="text-xs text-gray-400 dark:text-gray-400 uppercase tracking-wider font-semibold">
                        {getAuthorName(smbStories[0].author)}
                      </div>
                      <ArrowRight size={16} className="text-red-400 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300" />
                    </div>
                  </div>
                </Link>

                {/* Other Articles - Modern */}
                {smbStories.slice(1).map((story, index) => (
                  <Link
                    key={story._id || story.id || index}
                    href={`/post?slug=${story.slug}`}
                    className="group block pb-6 border-b border-gray-200/50 dark:border-slate-700/50 last:border-0 hover:bg-white/50 dark:hover:bg-slate-800/30 p-4 rounded-xl transition-all duration-300 hover:scale-[1.01] backdrop-blur-sm"
                  >
                    <h4 className="text-sm font-bold text-gray-900 dark:text-white mb-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-300 line-clamp-2">
                      {story.title}
                    </h4>
                    <div className="flex items-center justify-between">
                      <div className="text-xs text-gray-600 dark:text-gray-400 uppercase tracking-wide font-semibold">
                        {getAuthorName(story.author)}
                      </div>
                      <ArrowRight size={12} className="text-red-600 dark:text-red-400 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300" />
                    </div>
                  </Link>
                ))}
              </>
            ) : (
              <div className="bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm p-6 rounded-xl border border-gray-200/50 dark:border-slate-700/50 text-center">
                <p className="text-sm text-gray-500 dark:text-gray-400">No SMB stories selected</p>
                <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">Select posts in the admin panel</p>
              </div>
            )}
          </div>

          {/* Column 2: Her Story - Modern 2026 */}
          <div className="space-y-6">
            <div className="bg-gradient-to-r from-gray-900 to-gray-800 dark:from-white dark:to-gray-100 px-5 py-4 rounded-xl shadow-lg border border-gray-800/50 dark:border-gray-200/50 backdrop-blur-sm">
              <h3 className="text-white dark:text-gray-900 font-black text-base uppercase tracking-widest">Her Story</h3>
            </div>

            {herStories.length > 0 ? (
              herStories.map((story, index) => (
                <Link
                  key={story._id || story.id || index}
                  href={`/post?slug=${story.slug}`}
                  className="group flex gap-4 pb-6 border-b border-gray-200/50 dark:border-slate-700/50 last:border-0 hover:bg-white/50 dark:hover:bg-slate-800/30 p-4 rounded-xl transition-all duration-300 hover:scale-[1.01] backdrop-blur-sm"
                >
                  <div className="flex-1 min-w-0">
                    <h4 className="text-sm font-bold text-gray-900 dark:text-white mb-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-300 line-clamp-2">
                      {story.title}
                    </h4>
                    <div className="flex items-center gap-2 text-xs text-gray-600 dark:text-gray-400 uppercase tracking-wide font-semibold">
                      <User size={12} />
                      <span>{getAuthorName(story.author)}</span>
                    </div>
                  </div>
                  <div className="flex-shrink-0 w-20 h-20 rounded-xl overflow-hidden bg-gray-100 dark:bg-slate-800 shadow-lg group-hover:shadow-xl transition-all duration-300 border border-gray-200/50 dark:border-slate-700/50">
                    {story.featuredImage ? (
                      <Image
                        src={story.featuredImage}
                        alt={story.title}
                        width={80}
                        height={80}
                        className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                      />
                    ) : (
                      <div className="w-full h-full bg-gradient-to-br from-pink-400 to-pink-500 dark:from-pink-600 dark:to-pink-700"></div>
                    )}
                  </div>
                </Link>
              ))
            ) : (
              <div className="bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm p-6 rounded-xl border border-gray-200/50 dark:border-slate-700/50 text-center">
                <p className="text-sm text-gray-500 dark:text-gray-400">No Her stories selected</p>
                <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">Select posts in the admin panel</p>
              </div>
            )}
          </div>

          {/* Column 3: Social Story - Modern 2026 */}
          <div className="space-y-6">
            <div className="bg-gradient-to-r from-gray-900 to-gray-800 dark:from-white dark:to-gray-100 px-5 py-4 rounded-xl shadow-lg border border-gray-800/50 dark:border-gray-200/50 backdrop-blur-sm">
              <h3 className="text-white dark:text-gray-900 font-black text-base uppercase tracking-widest">Social Story</h3>
            </div>

            {socialStories.length > 0 ? (
              <>
                {/* Main Article with Large Image - Modern */}
                <Link href={`/post?slug=${socialStories[0].slug}`} className="group block">
                  <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-800 rounded-xl shadow-xl mb-4 group-hover:shadow-2xl transition-all duration-500 border border-gray-200/50 dark:border-slate-700/50 group-hover:scale-[1.02]">
                    {socialStories[0].featuredImage ? (
                      <Image
                        src={socialStories[0].featuredImage}
                        alt={socialStories[0].title}
                        fill
                        className="object-cover group-hover:scale-110 transition-transform duration-700"
                        sizes="(max-width: 768px) 100vw, 33vw"
                      />
                    ) : (
                      <div className="w-full h-full bg-gradient-to-br from-green-400 to-green-500 dark:from-green-600 dark:to-green-700"></div>
                    )}
                    <div className="absolute top-4 left-4">
                      <span className="inline-flex items-center justify-center w-12 h-12 bg-gradient-to-br from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 text-white font-black rounded-full text-sm shadow-xl group-hover:scale-110 transition-transform duration-300">
                        1
                      </span>
                    </div>
                    <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/95 via-black/80 to-transparent p-5 backdrop-blur-sm">
                      <h4 className="text-white font-bold text-base line-clamp-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-300 group-hover:to-red-200 transition-all duration-500">
                        {socialStories[0].title}
                      </h4>
                    </div>
                  </div>
                </Link>

                {/* Other Articles with Numbers - Modern */}
                {socialStories.slice(1).map((story, index) => (
                  <Link
                    key={story._id || story.id || index}
                    href={`/post?slug=${story.slug}`}
                    className="group flex gap-3 pb-6 border-b border-gray-200/50 dark:border-slate-700/50 last:border-0 hover:bg-white/50 dark:hover:bg-slate-800/30 p-4 rounded-xl transition-all duration-300 hover:scale-[1.01] backdrop-blur-sm"
                  >
                    <span className="flex-shrink-0 inline-flex items-center justify-center w-8 h-8 bg-gradient-to-br from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 text-white font-black rounded-full text-[10px] shadow-lg group-hover:scale-110 group-hover:shadow-xl transition-all duration-300">
                      {index + 2}
                    </span>
                    <h4 className="text-sm font-bold text-gray-900 dark:text-white group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-300 line-clamp-2 flex-1">
                      {story.title}
                    </h4>
                    <ArrowRight size={12} className="text-red-600 dark:text-red-400 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300 flex-shrink-0" />
                  </Link>
                ))}
              </>
            ) : (
              <div className="bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm p-6 rounded-xl border border-gray-200/50 dark:border-slate-700/50 text-center">
                <p className="text-sm text-gray-500 dark:text-gray-400">No Social stories selected</p>
                <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">Select posts in the admin panel</p>
              </div>
            )}
          </div>
        </div>
      </div>
    </section>
  );
}
