"use client";

import { useState, useEffect, useMemo } from "react";
import Link from "next/link";
import { User } from "lucide-react";
import axios from "axios";

export default function SpotLight() {
  const [spotlightContent, setSpotlightContent] = useState([]);
  const [spotlightSettings, setSpotlightSettings] = useState({
    title: "SpotLight",
    subtitle: "Featured Stories & Insights",
    showSection: true,
  });

  useEffect(() => {
    let isMounted = true;

    const loadSpotlightContent = async () => {
      try {
        // Fetch spotlight section data from dedicated spotlight API endpoint
        const response = await axios.get("/api/settings/spotlight", {
          timeout: 5000,
        });
        if (!isMounted) return;

        // Spotlight section data comes from dedicated endpoint
        if (response.data) {
          if (response.data.spotlight && Array.isArray(response.data.spotlight)) {
            setSpotlightContent(response.data.spotlight);
          } else {
            setSpotlightContent([]);
          }
          
          // Load settings from API
          if (response.data.settings) {
            setSpotlightSettings(response.data.settings);
          }
        } else {
          setSpotlightContent([]);
        }
      } catch (error) {
        console.error("Error loading spotlight content from API:", error);
        if (isMounted) {
          setSpotlightContent([]);
        }
      }
    };

    loadSpotlightContent();

    return () => {
      isMounted = false;
    };
  }, []);

  // Use admin-selected posts from database only
  const spotlightPosts = useMemo(() => {
    if (spotlightContent && Array.isArray(spotlightContent) && spotlightContent.length > 0) {
      // Ensure all posts have required fields
      return spotlightContent.map((post) => ({
        ...post,
        slug: post.slug || (post._id ? `post-${post._id}` : `post-${post.id}`),
        featuredImage: post.featuredImage || "/demo.png",
        category: post.category || "Uncategorized",
        author: post.author || { name: "Editorial Team" },
        excerpt: post.excerpt || "",
      }));
    }
    return []; // Return empty array if no posts selected
  }, [spotlightContent]);

  // Don't render if section is disabled
  if (spotlightSettings.showSection === false) {
    return null;
  }

  const getAuthorName = (author) => {
    if (!author) return "Editorial Team";
    if (typeof author === "object" && author?.name) {
      return author.name;
    }
    return author;
  };

  // Don't render if section is disabled
  if (spotlightSettings.showSection === false) {
    return null;
  }

  return (
    <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20 overflow-hidden">
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header - Modern 2026 Style */}
        <div className="mb-16">
          <div className="mb-8">
            <h2 className="text-5xl md:text-6xl lg:text-7xl font-black text-transparent bg-clip-text bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-200 dark:to-white tracking-tight leading-tight">
              {spotlightSettings.title || "SpotLight"}
              <span className="block mt-2 text-2xl md:text-3xl lg:text-4xl font-bold text-gray-500 dark:text-gray-400 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 bg-clip-text text-transparent">
                Light
              </span>
            </h2>
          </div>
          
          {/* Subtitle */}
          {spotlightSettings.subtitle && (
            <div className="mb-12">
              <p className="text-sm md:text-base text-gray-600 dark:text-gray-400 font-medium uppercase tracking-widest">
                {spotlightSettings.subtitle}
              </p>
            </div>
          )}
        </div>

        {/* Article Cards Grid - Modern 2026 Design */}
        {spotlightPosts.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8">
            {spotlightPosts.map((post, index) => (
              <Link
                key={post._id || post.id || index}
                href={`/post?slug=${post.slug}`}
                className="group relative bg-white/80 dark:bg-slate-900/80 backdrop-blur-xl rounded-xl overflow-hidden shadow-lg hover:shadow-2xl border border-gray-200/50 dark:border-slate-700/50 transition-all duration-500 hover:scale-[1.02] hover:-translate-y-1"
              >
                {/* Gradient Border Effect */}
                <div className="absolute inset-0 bg-gradient-to-r from-red-500/0 via-red-500/0 to-red-500/0 group-hover:from-red-500/10 group-hover:via-red-500/5 group-hover:to-red-500/10 transition-all duration-500 rounded-xl"></div>
                
                {/* Left Accent Bar */}
                <div className="absolute left-0 top-0 bottom-0 w-1 bg-gradient-to-b from-red-500 via-red-600 to-red-500 dark:from-red-400 dark:via-red-500 dark:to-red-400 opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>

                {/* Card Content Container */}
                <div className="relative flex flex-col h-full p-5">
                  {/* Category Badge */}
                  <div className="mb-3">
                    <span className="inline-block px-3 py-1 text-[10px] font-bold text-red-600 dark:text-red-400 bg-gradient-to-r from-red-50 to-red-50/50 dark:from-red-950/30 dark:to-red-950/10 rounded-lg uppercase tracking-widest border border-red-200/50 dark:border-red-800/50">
                      {post.category}
                    </span>
                  </div>

                  {/* Title */}
                  <h3 className="text-base font-bold text-gray-900 dark:text-white leading-tight mb-3 line-clamp-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-500">
                    {post.title}
                  </h3>

                  {/* Description/Excerpt */}
                  {post.excerpt && (
                    <p className="text-xs text-gray-600 dark:text-gray-400 leading-relaxed mb-4 line-clamp-2">
                      {post.excerpt}
                    </p>
                  )}

                  {/* Footer - Author and Date */}
                  <div className="flex items-center justify-between text-[10px] text-gray-500 dark:text-gray-400 mt-auto pt-4 border-t border-gray-200/50 dark:border-slate-700/50">
                    <div className="flex items-center gap-2">
                      <div className="w-6 h-6 rounded-full bg-gradient-to-br from-gray-200 to-gray-300 dark:from-slate-700 dark:to-slate-600 flex items-center justify-center shadow-sm group-hover:shadow-md transition-shadow duration-300">
                        <User size={9} className="text-gray-500 dark:text-gray-400" />
                      </div>
                      <span className="truncate font-semibold uppercase tracking-wide">{getAuthorName(post.author)}</span>
                    </div>
                    {/* Date */}
                    {post.publishedAt && (
                      <span className="text-gray-400 dark:text-gray-500 whitespace-nowrap uppercase tracking-wider text-[9px]">
                        {new Date(post.publishedAt).toLocaleDateString('en-US', { 
                          month: 'short', 
                          day: 'numeric', 
                          year: 'numeric' 
                        })}
                      </span>
                    )}
                  </div>

                  {/* Hover Glow Effect */}
                  <div className="absolute -inset-0.5 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 rounded-xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-500 -z-10"></div>
                </div>
              </Link>
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <p className="text-gray-500 dark:text-gray-400 text-lg">No spotlight posts selected</p>
            <p className="text-sm text-gray-400 dark:text-gray-500 mt-2">Select posts in the admin panel to display them here</p>
          </div>
        )}
      </div>
    </section>
  );
}
