"use client";

import { useState, useEffect, useMemo } from "react";
import Image from "next/image";
import Link from "next/link";
import { TrendingUp, Clock, ArrowRight } from "lucide-react";
import axios from "axios";

export default function TrendingNow() {
  const [trendingContent, setTrendingContent] = useState(null);

  useEffect(() => {
    let isMounted = true;

    const loadTrendingContent = async () => {
      try {
        const response = await axios.get("/api/settings/homepage");
        if (!isMounted) return;

        if (response.data && response.data.content && response.data.content.trending) {
          setTrendingContent(response.data.content.trending);
          return;
        }
      } catch (error) {
        console.error("Error loading trending content from API:", error);
      }

      // Set empty state if API fails or no data
      if (isMounted) {
        setTrendingContent([]);
      }
    };

    loadTrendingContent();

    return () => {
      isMounted = false;
    };
  }, []);

  const getAuthorName = (author) => {
    if (!author) return "Editorial Team";
    if (typeof author === "object" && author?.name) {
      return author.name;
    }
    return author;
  };

  const formatTimeAgo = (date) => {
    if (!date) return "Recently";
    const now = new Date();
    const postDate = new Date(date);
    const diffInSeconds = Math.floor((now - postDate) / 1000);
    
    if (diffInSeconds < 60) return "Just now";
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} minutes ago`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hours ago`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)} days ago`;
    return postDate.toLocaleDateString();
  };

  // Normalize and prepare trending articles
  const trendingArticles = useMemo(() => {
    if (!trendingContent || !Array.isArray(trendingContent) || trendingContent.length === 0) {
      return [];
    }

    return trendingContent.map((post) => ({
      ...post,
      slug: post.slug || (post._id ? `post-${post._id}` : `post-${post.id}`),
      featuredImage: post.featuredImage || "/demo.png",
      author: post.author || { name: "Editorial Team" },
      excerpt: post.excerpt || "",
      category: post.category || "Uncategorized",
      publishedAt: post.publishedAt || post.createdAt || new Date().toISOString(),
    }));
  }, [trendingContent]);

  return (
    <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20 relative overflow-hidden">
      {/* Decorative Background Elements */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-0 right-0 w-96 h-96 bg-red-200/10 dark:bg-red-900/5 rounded-full blur-3xl"></div>
        <div className="absolute bottom-0 left-0 w-96 h-96 bg-red-300/10 dark:bg-red-800/5 rounded-full blur-3xl"></div>
      </div>

      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header Section - Modern 2026 */}
        <div className="mb-16">
          <div className="flex items-center gap-4 mb-6">
            <div className="relative">
              <TrendingUp className="w-10 h-10 text-red-600 dark:text-red-400" />
              <div className="absolute -top-1 -right-1 w-4 h-4 bg-red-500 rounded-full animate-pulse"></div>
              <div className="absolute -inset-2 bg-red-500/20 rounded-full blur-lg opacity-0 hover:opacity-100 transition-opacity"></div>
            </div>
            <div>
              <h2 className="text-5xl md:text-6xl lg:text-7xl font-black text-transparent bg-clip-text bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-200 dark:to-white tracking-tight">
                Trending
                <span className="bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 bg-clip-text text-transparent ml-2">Now</span>
              </h2>
              <p className="text-gray-600 dark:text-gray-400 text-xs md:text-sm mt-3 uppercase tracking-widest font-medium">
                Latest stories making waves in Maharashtra's startup ecosystem
              </p>
            </div>
          </div>
        </div>

        {/* Trending Articles Grid - Modern 2026 */}
        {trendingArticles.length > 0 ? (
          <>
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-10">
              {/* Main Featured Article */}
              {trendingArticles[0] && (
                <Link
                  href={`/post?slug=${trendingArticles[0].slug}`}
                  className="group lg:col-span-1 bg-white/80 dark:bg-slate-900/80 backdrop-blur-xl rounded-xl overflow-hidden shadow-xl hover:shadow-2xl transition-all duration-500 hover:scale-[1.02] hover:-translate-y-1 border border-gray-200/50 dark:border-slate-700/50"
                >
                  <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-700">
                    {trendingArticles[0].featuredImage ? (
                      <Image
                        src={trendingArticles[0].featuredImage}
                        alt={trendingArticles[0].title}
                        fill
                        className="object-cover group-hover:scale-110 transition-transform duration-700"
                        sizes="(max-width: 1024px) 100vw, 50vw"
                      />
                    ) : (
                      <div className="w-full h-full bg-gradient-to-br from-red-400 to-red-600 dark:from-red-600 dark:to-red-700"></div>
                    )}
                    <div className="absolute top-4 left-4">
                      <span className="inline-flex items-center gap-1.5 px-4 py-2 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 text-white text-xs font-black rounded-full shadow-xl uppercase tracking-widest">
                        <TrendingUp size={12} />
                        #1 Trending
                      </span>
                    </div>
                    <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/95 via-black/80 to-transparent p-6 backdrop-blur-sm">
                      <div className="flex items-center gap-3 text-white/80 text-[10px] uppercase tracking-widest font-semibold mb-2">
                        <Clock size={11} />
                        <span>{formatTimeAgo(trendingArticles[0].publishedAt)}</span>
                      </div>
                      <h3 className="text-xl md:text-2xl font-black text-white line-clamp-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-300 group-hover:to-red-200 transition-all duration-500">
                        {trendingArticles[0].title}
                      </h3>
                    </div>
                  </div>
                  <div className="p-6">
                    {trendingArticles[0].excerpt && (
                      <p className="text-gray-600 dark:text-gray-300 text-sm leading-relaxed mb-4 line-clamp-3">
                        {trendingArticles[0].excerpt}
                      </p>
                    )}
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <span className="text-[10px] font-bold text-red-600 dark:text-red-400 uppercase tracking-widest">
                          {trendingArticles[0].category}
                        </span>
                        <span className="text-gray-400">•</span>
                        <span className="text-xs text-gray-600 dark:text-gray-400 uppercase tracking-wide font-semibold">{getAuthorName(trendingArticles[0].author)}</span>
                      </div>
                      <ArrowRight size={16} className="text-red-600 dark:text-red-400 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300" />
                    </div>
                  </div>
                  {/* Hover Glow */}
                  <div className="absolute -inset-0.5 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 rounded-xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-500 -z-10"></div>
                </Link>
              )}

              {/* Secondary Featured Article */}
              {trendingArticles[1] && (
                <Link
                  href={`/post?slug=${trendingArticles[1].slug}`}
                  className="group lg:col-span-1 bg-white/80 dark:bg-slate-900/80 backdrop-blur-xl rounded-xl overflow-hidden shadow-xl hover:shadow-2xl transition-all duration-500 hover:scale-[1.02] hover:-translate-y-1 border border-gray-200/50 dark:border-slate-700/50"
                >
                  <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-700">
                    {trendingArticles[1].featuredImage ? (
                      <Image
                        src={trendingArticles[1].featuredImage}
                        alt={trendingArticles[1].title}
                        fill
                        className="object-cover group-hover:scale-110 transition-transform duration-700"
                        sizes="(max-width: 1024px) 100vw, 50vw"
                      />
                    ) : (
                      <div className="w-full h-full bg-gradient-to-br from-red-400 to-red-600 dark:from-red-600 dark:to-red-700"></div>
                    )}
                    <div className="absolute top-4 left-4">
                      <span className="inline-flex items-center gap-1.5 px-4 py-2 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 text-white text-xs font-black rounded-full shadow-xl uppercase tracking-widest">
                        <TrendingUp size={12} />
                        #2 Trending
                      </span>
                    </div>
                    <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/95 via-black/80 to-transparent p-6 backdrop-blur-sm">
                      <div className="flex items-center gap-3 text-white/80 text-[10px] uppercase tracking-widest font-semibold mb-2">
                        <Clock size={11} />
                        <span>{formatTimeAgo(trendingArticles[1].publishedAt)}</span>
                      </div>
                      <h3 className="text-xl md:text-2xl font-black text-white line-clamp-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-300 group-hover:to-red-200 transition-all duration-500">
                        {trendingArticles[1].title}
                      </h3>
                    </div>
                  </div>
                  <div className="p-6">
                    {trendingArticles[1].excerpt && (
                      <p className="text-gray-600 dark:text-gray-300 text-sm leading-relaxed mb-4 line-clamp-3">
                        {trendingArticles[1].excerpt}
                      </p>
                    )}
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <span className="text-[10px] font-bold text-red-600 dark:text-red-400 uppercase tracking-widest">
                          {trendingArticles[1].category}
                        </span>
                        <span className="text-gray-400">•</span>
                        <span className="text-xs text-gray-600 dark:text-gray-400 uppercase tracking-wide font-semibold">{getAuthorName(trendingArticles[1].author)}</span>
                      </div>
                      <ArrowRight size={16} className="text-red-600 dark:text-red-400 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300" />
                    </div>
                  </div>
                  {/* Hover Glow */}
                  <div className="absolute -inset-0.5 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 rounded-xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-500 -z-10"></div>
                </Link>
              )}
            </div>

            {/* Other Trending Articles - Modern 2026 */}
            {trendingArticles.length > 2 && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                {trendingArticles.slice(2).map((article, index) => (
                  <Link
                    key={article._id || article.id || index}
                    href={`/post?slug=${article.slug}`}
                    className="group relative bg-white/80 dark:bg-slate-900/80 backdrop-blur-xl rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-500 hover:scale-[1.03] hover:-translate-y-1 border border-gray-200/50 dark:border-slate-700/50"
                  >
                    <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-700">
                      {article.featuredImage ? (
                        <Image
                          src={article.featuredImage}
                          alt={article.title}
                          fill
                          className="object-cover group-hover:scale-110 transition-transform duration-500"
                          sizes="(max-width: 768px) 100vw, (max-width: 1024px) 50vw, 25vw"
                        />
                      ) : (
                        <div className="w-full h-full bg-gradient-to-br from-red-400 to-red-600 dark:from-red-600 dark:to-red-700"></div>
                      )}
                      <div className="absolute top-3 right-3">
                        <span className="inline-flex items-center justify-center w-9 h-9 bg-gradient-to-br from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 text-white text-[10px] font-black rounded-full shadow-xl group-hover:scale-110 transition-transform duration-300">
                          {index + 3}
                        </span>
                      </div>
                    </div>
                    <div className="p-4">
                      <div className="flex items-center gap-2 text-[10px] text-gray-500 dark:text-gray-400 mb-2 uppercase tracking-widest font-semibold">
                        <Clock size={9} />
                        <span>{formatTimeAgo(article.publishedAt)}</span>
                      </div>
                      <h4 className="text-sm font-bold text-gray-900 dark:text-white mb-2 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-500 line-clamp-2">
                        {article.title}
                      </h4>
                      <div className="flex items-center justify-between">
                        <span className="text-[10px] font-bold text-red-600 dark:text-red-400 uppercase tracking-widest">
                          {article.category}
                        </span>
                        <ArrowRight size={12} className="text-red-600 dark:text-red-400 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300" />
                      </div>
                    </div>
                    {/* Hover Glow */}
                    <div className="absolute -inset-0.5 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 rounded-xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-500 -z-10"></div>
                  </Link>
                ))}
              </div>
            )}
          </>
        ) : (
          <div className="bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm rounded-xl p-12 text-center border border-gray-200/50 dark:border-slate-700/50">
            <p className="text-gray-500 dark:text-gray-400 text-base mb-2">No trending posts selected</p>
            <p className="text-xs text-gray-400 dark:text-gray-500 uppercase tracking-wider">Select posts in the admin panel</p>
          </div>
        )}
      </div>
    </section>
  );
}
