"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Compass } from "lucide-react";
import axios from "axios";

export default function Explore() {
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        setLoading(true);
        const response = await axios.get("/api/public/categories");
        
        if (response.data && Array.isArray(response.data.categories)) {
          setCategories(response.data.categories);
        } else {
          console.warn("Categories data format unexpected:", response.data);
          setCategories([]);
        }
      } catch (error) {
        console.error("Error fetching categories:", error);
        console.error("Error details:", {
          message: error.message,
          name: error.name,
        });
        setCategories([]);
      } finally {
        setLoading(false);
      }
    };

    fetchCategories();
  }, []);

  const getCategorySlug = (categoryName) => {
    return categoryName.toLowerCase().replace(/\s+/g, '-').replace(/[^a-z0-9-]/g, '');
  };

  return (
    <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20">
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header Section - Modern 2026 */}
        <div className="text-center mb-16">
          <div className="relative inline-block mb-6">
            <h2 className="text-6xl md:text-7xl lg:text-8xl font-black text-transparent bg-clip-text bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-200 dark:to-white tracking-tight relative">
              EXPLO
              <span className="relative inline-block mx-1">
                <span className="relative z-10 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 bg-clip-text text-transparent">R</span>
                {/* Circular text around R */}
                <span className="absolute -top-6 -left-16 text-[10px] md:text-xs font-bold text-red-500 dark:text-red-400 whitespace-nowrap transform -rotate-12 uppercase tracking-widest">
                  MSO PREDICTS
                </span>
                <span className="absolute -bottom-6 -right-16 text-[10px] md:text-xs font-bold text-red-500 dark:text-red-400 whitespace-nowrap transform rotate-12 uppercase tracking-widest">
                  MSO
                </span>
              </span>
              E
            </h2>
          </div>
          <p className="text-gray-600 dark:text-gray-300 text-sm md:text-base mt-6 font-medium uppercase tracking-widest">
            Discover emerging startups from Maharashtra and explore the innovation ecosystem
          </p>
        </div>

        {/* Categories Grid - Modern 2026 */}
        {loading ? (
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6 md:gap-8">
            {[...Array(8)].map((_, i) => (
              <div
                key={i}
                className="animate-pulse aspect-square rounded-xl bg-gray-200/50 dark:bg-slate-700/50 backdrop-blur-sm border border-gray-200/50 dark:border-slate-700/50"
              ></div>
            ))}
          </div>
        ) : categories.length > 0 ? (
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6 md:gap-8">
            {categories.map((category) => (
              <Link
                key={category.name}
                href={`/explore/${getCategorySlug(category.name)}`}
                className="group relative overflow-hidden rounded-xl bg-white/80 dark:bg-slate-900/80 backdrop-blur-xl border border-gray-200/50 dark:border-slate-700/50 hover:border-red-500/50 dark:hover:border-red-400/50 hover:shadow-2xl transition-all duration-500 aspect-square hover:scale-[1.05] hover:-translate-y-1"
              >
                {/* Background Gradient */}
                <div className="absolute inset-0">
                  <div className="w-full h-full bg-gradient-to-br from-red-500/0 via-red-500/0 to-red-500/0 group-hover:from-red-500/10 group-hover:via-red-500/5 group-hover:to-red-500/10 dark:group-hover:from-red-400/10 dark:group-hover:via-red-400/5 dark:group-hover:to-red-400/10 transition-all duration-500"></div>
                  <div className="absolute inset-0 bg-gradient-to-t from-white/95 dark:from-slate-900/95 via-white/80 dark:via-slate-900/80 to-transparent"></div>
                </div>

                {/* Hover Glow Effect */}
                <div className="absolute -inset-0.5 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 rounded-xl opacity-0 group-hover:opacity-20 blur-xl transition-opacity duration-500 -z-10"></div>

                {/* Content */}
                <div className="relative h-full flex flex-col items-center justify-center p-6 text-center">
                  <div className="relative mb-4">
                    <Compass className="w-10 h-10 md:w-12 md:h-12 text-red-600 dark:text-red-400 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-500 group-hover:to-red-600 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-500 group-hover:scale-110 group-hover:rotate-12" />
                    <div className="absolute inset-0 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 rounded-full opacity-0 group-hover:opacity-20 blur-lg transition-opacity duration-500"></div>
                  </div>
                  <h3 className="text-gray-900 dark:text-white font-black text-sm md:text-base mb-3 group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-500 uppercase tracking-wide">
                    {category.name}
                  </h3>
                  <div className="text-[10px] text-gray-500 dark:text-gray-400 uppercase tracking-widest font-semibold">
                    <span className="font-black">{category.count || 0}</span>
                    <span className="ml-1">posts</span>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        ) : (
          <div className="bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm rounded-xl p-12 text-center border border-gray-200/50 dark:border-slate-700/50">
            <p className="text-gray-500 dark:text-gray-400 text-base mb-2">No categories available</p>
            <p className="text-xs text-gray-400 dark:text-gray-500 uppercase tracking-wider">Categories will appear here once posts are published</p>
          </div>
        )}
      </div>
    </section>
  );
}
