"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Lightbulb } from "lucide-react";
import axios from "axios";

export default function DiscoverMore() {
  const [categoriesWithPosts, setCategoriesWithPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadDiscoverPosts = async () => {
      try {
        setLoading(true);
        
        // Fetch all published posts
        const response = await axios.get("/api/public/posts?limit=100");
        const posts = response.data.posts || [];
        
        if (posts.length === 0) {
          setCategoriesWithPosts([]);
          setLoading(false);
          return;
        }
        
        // Group posts by category
        const categoryMap = {};
        posts.forEach((post) => {
          if (post.category) {
            if (!categoryMap[post.category]) {
              categoryMap[post.category] = [];
            }
            categoryMap[post.category].push(post);
          }
        });
        
        // Filter categories that have at least 3 posts
        const validCategories = Object.keys(categoryMap).filter(
          (cat) => categoryMap[cat].length >= 3
        );
        
        // Select exactly 9 random categories (or all if less than 9)
        const selectedCategories = validCategories
          .sort(() => Math.random() - 0.5) // Shuffle
          .slice(0, 9); // Limit to 9 categories
        
        // Build category data with 3 posts each
        const categoriesData = selectedCategories.map((categoryName) => ({
          name: categoryName,
          posts: categoryMap[categoryName]
            .sort(() => Math.random() - 0.5) // Shuffle posts
            .slice(0, 3), // Take only 3 posts
        }));
        
        setCategoriesWithPosts(categoriesData);
      } catch (error) {
        console.error("Error loading discover posts:", error);
        setCategoriesWithPosts([]);
      } finally {
        setLoading(false);
      }
    };

    loadDiscoverPosts();
  }, []);

  return (
    <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20">
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header - Modern 2026 */}
        <div className="text-center mb-16">
          <h2 className="text-5xl md:text-6xl lg:text-7xl font-black text-transparent bg-clip-text bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-200 dark:to-white tracking-tight inline-flex items-center">
            Discover M
            <span className="relative inline-block mx-1">
              <span className="relative z-10">o</span>
              <Lightbulb className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-8 h-8 md:w-10 md:h-10 text-red-500 dark:text-red-400" fill="currentColor" />
            </span>
            re
          </h2>
        </div>

        {/* Categories Grid - Modern 2026 */}
        {loading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-red-600 dark:border-red-400 mx-auto"></div>
            <p className="text-gray-500 dark:text-gray-400 mt-4 text-xs uppercase tracking-widest font-semibold">Loading posts...</p>
          </div>
        ) : categoriesWithPosts.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {categoriesWithPosts.map((category, index) => (
              <div key={category.name || index} className="space-y-5 bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm rounded-xl p-6 border border-gray-200/50 dark:border-slate-700/50 hover:bg-white/80 dark:hover:bg-slate-800/50 transition-all duration-300 hover:scale-[1.02]">
                {/* Category Header */}
                <div>
                  <h3 className="text-lg font-black text-gray-900 dark:text-white mb-3 uppercase tracking-wider bg-gradient-to-r from-gray-900 to-gray-800 dark:from-white dark:to-gray-200 bg-clip-text text-transparent group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-500">
                    {category.name}
                  </h3>
                  <div className="h-0.5 bg-gradient-to-r from-red-500 via-red-600 to-transparent dark:from-red-400 dark:via-red-500 dark:to-transparent rounded-full"></div>
                </div>

                {/* Posts List - Modern */}
                {category.posts && category.posts.length > 0 ? (
                  <ul className="space-y-3">
                    {category.posts.map((post, postIndex) => (
                      <li key={post._id || post.slug || postIndex}>
                        <Link
                          href={`/post?slug=${post.slug}`}
                          className="block text-sm text-gray-700 dark:text-gray-300 hover:text-transparent hover:bg-clip-text hover:bg-gradient-to-r hover:from-red-600 hover:to-red-500 dark:hover:from-red-400 dark:hover:to-red-500 transition-all duration-300 leading-relaxed font-medium group/item relative pl-4 border-l-2 border-transparent hover:border-red-500 dark:hover:border-red-400"
                        >
                          <span className="relative z-10">{post.title}</span>
                        </Link>
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="text-xs text-gray-400 dark:text-gray-500 uppercase tracking-wider italic">No posts available</p>
                )}
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12 bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm rounded-xl border border-gray-200/50 dark:border-slate-700/50">
            <p className="text-gray-500 dark:text-gray-400 text-base">No posts available</p>
            <p className="text-xs text-gray-400 dark:text-gray-500 mt-2 uppercase tracking-wider">Publish some posts to see them here</p>
          </div>
        )}
      </div>
    </section>
  );
}
