"use client";

import { useState, useEffect, useMemo } from "react";
import Image from "next/image";
import Link from "next/link";
import { ArrowRight } from "lucide-react";
import axios from "axios";

export default function Recent() {
  const [recentPosts, setRecentPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Fetch recent published posts from API
    const loadRecentPosts = async () => {
      try {
        setLoading(true);
        const response = await axios.get("/api/public/posts?limit=6&sort=publishedAt");
        if (response.data.posts && Array.isArray(response.data.posts)) {
          setRecentPosts(response.data.posts);
        } else {
          setRecentPosts([]);
        }
      } catch (error) {
        console.error("Error loading recent posts:", error);
        setRecentPosts([]);
      } finally {
        setLoading(false);
      }
    };

    loadRecentPosts();
  }, []);

  const getAuthorName = (author) => {
    if (!author) return "Editorial Team";
    if (typeof author === "object" && author?.name) {
      return author.name;
    }
    return author;
  };

  const formatDate = (dateString) => {
    if (!dateString) return "Recently";
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", {
      year: "numeric",
      month: "long",
      day: "numeric",
    });
  };

  const formatTimeAgo = (date) => {
    if (!date) return "Recently";
    const now = new Date();
    const postDate = new Date(date);
    const diffInSeconds = Math.floor((now - postDate) / 1000);
    
    if (diffInSeconds < 60) return "Just now";
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} minutes ago`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hours ago`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)} days ago`;
    return formatDate(date);
  };

  // Normalize and prepare recent posts
  const normalizedPosts = useMemo(() => {
    return recentPosts.map((post) => ({
      ...post,
      slug: post.slug || (post._id ? `post-${post._id}` : `post-${post.id}`),
      featuredImage: post.featuredImage || "/demo.png",
      author: post.author || { name: "Editorial Team" },
      excerpt: post.excerpt || "",
      category: post.category || "Uncategorized",
      publishedAt: post.publishedAt || post.createdAt || new Date().toISOString(),
    }));
  }, [recentPosts]);

  if (loading) {
    return (
      <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20">
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mb-12">
            <div className="h-12 bg-gray-200/50 dark:bg-slate-700/30 rounded-xl w-64 animate-pulse backdrop-blur-sm"></div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {[...Array(6)].map((_, i) => (
              <div key={i} className="flex gap-4 p-4 bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm rounded-xl border border-gray-200/50 dark:border-slate-700/50">
                <div className="w-20 h-20 bg-gray-200/50 dark:bg-slate-700/30 rounded-lg animate-pulse shrink-0"></div>
                <div className="flex-1 space-y-2">
                  <div className="h-4 bg-gray-200/50 dark:bg-slate-700/30 rounded w-3/4 animate-pulse"></div>
                  <div className="h-3 bg-gray-200/50 dark:bg-slate-700/30 rounded w-full animate-pulse"></div>
                  <div className="h-3 bg-gray-200/50 dark:bg-slate-700/30 rounded w-2/3 animate-pulse"></div>
                  <div className="h-3 bg-gray-200/50 dark:bg-slate-700/30 rounded w-1/2 animate-pulse"></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  if (normalizedPosts.length === 0) {
    return null; // Don't show section if no posts
  }

  return (
    <section className="relative bg-gradient-to-b from-white via-gray-50/30 to-white dark:from-slate-900 dark:via-slate-800/30 dark:to-slate-900 border-b border-gray-200/50 dark:border-slate-700/50 py-20">
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header - Modern 2026 */}
        <div className="mb-12">
          <h2 className="text-5xl md:text-6xl lg:text-7xl font-black text-transparent bg-clip-text bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-200 dark:to-white tracking-tight">
            Recent Posts
          </h2>
          <div className="mt-3 h-1 w-32 bg-gradient-to-r from-red-500 via-red-600 to-transparent dark:from-red-400 dark:via-red-500 dark:to-transparent rounded-full"></div>
        </div>

        {/* Recent Posts - Modern 2026 Design */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {normalizedPosts.map((post, index) => (
            <Link
              key={post._id || post.id || index}
              href={`/post?slug=${post.slug}`}
              className="group flex gap-4 p-4 bg-white/50 dark:bg-slate-800/30 backdrop-blur-sm hover:bg-white/80 dark:hover:bg-slate-800/50 transition-all duration-300 rounded-xl border border-gray-200/50 dark:border-slate-700/50 hover:border-red-500/30 dark:hover:border-red-400/30 hover:shadow-lg hover:scale-[1.01]"
            >
              {/* Image */}
              <div className="relative w-20 h-20 shrink-0 rounded-lg overflow-hidden bg-gray-100 dark:bg-slate-700 border border-gray-200/50 dark:border-slate-700/50 shadow-sm group-hover:shadow-md transition-shadow duration-300">
                {post.featuredImage ? (
                  <Image
                    src={post.featuredImage}
                    alt={post.title}
                    fill
                    className="object-cover group-hover:scale-110 transition-transform duration-500"
                    sizes="80px"
                  />
                ) : (
                  <div className="w-full h-full bg-gradient-to-br from-red-400 to-red-600 dark:from-red-600 dark:to-red-700"></div>
                )}
              </div>
              
              {/* Content */}
              <div className="flex-1 min-w-0">
                <h3 className="text-sm font-bold text-gray-900 dark:text-white group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red-600 group-hover:to-red-500 dark:group-hover:from-red-400 dark:group-hover:to-red-500 transition-all duration-300 line-clamp-1 mb-2">
                  {post.title}
                </h3>
                {post.excerpt && (
                  <p className="text-xs text-gray-600 dark:text-gray-400 line-clamp-2 mb-2.5 leading-relaxed">
                    {post.excerpt.length > 120 ? post.excerpt.substring(0, 120) + '...' : post.excerpt}
                  </p>
                )}
                <div className="flex items-center gap-2 text-[10px] text-gray-500 dark:text-gray-400 uppercase tracking-widest font-semibold">
                  <span>{formatTimeAgo(post.publishedAt)}</span>
                  <span className="text-gray-300 dark:text-gray-600">•</span>
                  <span className="text-[10px] font-bold text-red-600 dark:text-red-400 uppercase tracking-widest truncate">
                    {post.category}
                  </span>
                </div>
              </div>
            </Link>
          ))}
        </div>

        {/* View All Link - Modern 2026 */}
        {normalizedPosts.length >= 6 && (
          <div className="mt-8 text-center">
            <Link
              href="/explore"
              className="inline-flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-red-500 to-red-600 dark:from-red-400 dark:to-red-500 text-white text-sm font-bold uppercase tracking-widest rounded-full shadow-lg hover:shadow-xl hover:scale-105 transition-all duration-300"
            >
              View All Posts
              <ArrowRight size={16} className="group-hover:translate-x-1 transition-transform duration-300" />
            </Link>
          </div>
        )}
      </div>
    </section>
  );
}
